.nav-box {
    grid-area: header;
    background-color: white;
    min-height: 6rem;
}

.nav-menu-close {
    display: grid;
    grid-template-columns: repeat(12 1fr);
}

.nav-menu-open {
    display: grid;
    grid-template-columns: repeat(12 1fr);
}

.skip-link {
    border: 1px solid black;
    background-color: opacity;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;

    color: #272727;
    font-weight: 700;
    left: 2%;
    padding: 4px;
    position: absolute;
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
}

.skip-link:focus {
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
}

.skip-link {
    transition: -webkit-transform 0.3s;
    transition: transform 0.3s;
    transition: transform 0.3s, -webkit-transform 0.3s;
}

.hamburger {
    width: 3rem;
    height: 3rem;
    padding-bottom: 3.8rem;

    font-size: 3rem;
    border: none;
    background-color: white;

    z-index: 12;
    transition: margin .4s linear;
    margin-right: 0rem;
    margin-top: 1.5rem;
    grid-column: 12 / 13;
}

.closeBtn {
    grid-column: 12 / 13;

    font-size: 2rem;
    padding: -2rem;
    border: none;
    background-color: white;
}

/* ----- menu box-----*/
.open-menu-box {
    display: grid;
    grid-template-columns: repeat(12 1fr);
    -webkit-animation: fadein 1.5s;
            animation: fadein 1.5s;
    margin-right: 1rem;
}

@-webkit-keyframes fadein {

    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

@keyframes fadein {

    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

.larger-screen-menu-box {
    display: none;
}

.menu-box {
    grid-column: span 12;
    list-style: none;
}

.menu-item-box {
    text-align: right;
}

.menu-item-btn {
    border: none;
    background-color: white;
    padding: 0.5rem;
    font-size: .8rem;
}

.menu-item-btn:hover {
