    margin-bottom: 1rem;
}


@media (min-width: 650px) {
    .blog-two-box {
        width: 80%;
        margin: 0 auto;
    }

    .blog-one-text {
        margin-top: 3.5rem;
    }

    .blog-two-date {
        margin-top: .5rem;
        margin-bottom: 1rem;
        font-size: 1.1rem;
    }

    .blog-two-description {
        font-size: 1.1rem;
    }

    .blog-two-h4-title {
        font-size: 1.3rem;
        margin-top: 3rem;
    }
}
.slider-container {
    display: flex;
    flex-direction: row;
}

.push-chevron-left-btn {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    font-size: 2rem;
    background-color: #f3d9fd;
    margin: 10rem auto;
    border: none;
}

.push-chevron-right-btn {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    font-size: 2rem;
    background-color: #f3d9fd;
    margin: 10rem auto;
    border: none;
}

.slider-imgs-container {
    width: 80%;
    margin: 5rem auto;
}

.slider-img {
    width: 100%;
    margin: 0 auto;
    height: auto;
}

.current-img {
    width: 100%;
    top: 10rem;
    left: 1.5rem;
    align-items: center;
    margin: 2rem auto;
}

.hidden-img {
    display: none;
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.app {
  background-color: #f3d9fd;
  min-height: 100vh;

  display: grid;
  grid-template-areas:
    "header"
    "main"
    "footer";
  grid-template-columns: auto;
  grid-template-rows: auto 1fr auto;
}
